
import {Menu,message} from 'antd';
import {Link} from 'react-router-dom';
export default function menuAccount (props){
    const key = 'updatable';
    const {name} = props.data;
    const handleLogout = ()=>{
        message.loading({ content: 'ang ng xut...', key });
        setTimeout(()=>{
            localStorage.removeItem("token");
            props.refreshAccount();
            message.success({ content: 'ng xut thnh cng!', key, duration: 2 });
        },1000)
    }
    return(
        <Menu theme="light" style={{boxShadow:'2px 2px 50px #00000026', borderRadius:'4px'}}>
            <Menu.Item key="bill" >
                <Link to="/billfollow">
                n hng
                </Link>
            </Menu.Item>
            <Menu.Item key="profile" >
                <Link to="/profile">
                    {"Thng tin ti khon " }
                </Link>
            </Menu.Item>
            
            <Menu.Item key="logout" onClick={handleLogout}>
                ng xut
            </Menu.Item>
        </Menu>
    )
}