import React ,{useState} from 'react';
import {Form,Input,Button,PageHeader,Result } from 'antd';
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import * as FetchAPI from '../../util/fetchApi';
import {getUser} from '../../util/getUser';
import { useDispatch } from 'react-redux';
export default function Register(props){
    const {email} = props;
    const [name, setname] = useState();
    const [username, setusername] = useState();
    const [password, setpassword] = useState();
    const [confirmpass, setconfirmpass] = useState();
    const [success, setsuccess] = useState(false);
    const dispatch = useDispatch();

    const checkUserExist = async(_,value)=>{
        const data = {"username":value}
        const res = await FetchAPI.postDataAPI("/user/checkUsername",data);
        if(res.success){
            return Promise.resolve();
        }else{
            return Promise.reject(new Error('Tn ng nhp  tn ti, vui lng chn tn khc  !'));
        }
    }
    const handleRegister = async()=>{
        props.loading(true);
        const data = {"email":email,"password":password,"username":username,"name":name};
        const res = await FetchAPI.postDataAPI("/user/register",data);
        if(res.success){
            setsuccess(true);
            props.loading(false);
        }else{
            console.log("C li ri");
            props.loading(false);
        }
    }
    const handleLogin = async() =>{
        props.loading(true)
        const data = {"username":username,"password":password};
        const res = await FetchAPI.postDataAPI("/user/login",data);
        console.log(res);
        if(res.msg==="Error"){
            console.log("Tht bi")
        }else if(res.msg==="Success"){
            localStorage.setItem("token",res.token);
            finish(res.token)
        }
        props.loading(false);
    }
    const finish = async(token)=>{
        getUser(token,dispatch);
        props.refeshAccount();
        props.cancel();
    }
    const SignUp = ()=>(
        <div>
        <PageHeader
            onBack={() => props.back()}
            title={"Tip tc ng k vi "}
            subTitle={email}
            style={{padding:0}}
        />
        <Form 
            style={{ paddingBottom:40,paddingTop:20,display:'flex',flexDirection:'column',alignItems:'center' }} 
            labelCol={{ span: 5 }}
            wrapperCol={{ span: 14 }}
            onFinish={handleRegister}
            scrollToFirstError>
            <Form.Item
                label="H tn"
                name="name"
                rules={[{ required: true, message: 'Vui lng nhp tn ti khon!' }]}
                style={{width:'80%'}}
            >
                <Input
                    placeholder="Nhp y  h tn"
                    value={name}
                    defaultValue={name}
                    onChange= {(e)=>setname(e.target.value)}
                    maxLength={24}
                    style={{height:40}}
                />
            </Form.Item>
            <Form.Item
                label="Tn ng nhp"
                name="username"
                hasFeedback
                rules={[
                    { required: true, message: 'Vui lng nhp h tn !' },
                    { validator: checkUserExist}
                ]}
                style={{width:'80%'}}
            >
                <Input
                    placeholder="Nhp tn ti khon"
                    value={username}
                    defaultValue={username}
                    onChange= {(e)=>setusername(e.target.value)}
                    maxLength={24}
                    style={{height:40}}
                />
            </Form.Item>
            <Form.Item
                label="Mt khu"
                name="password"
                rules={[
                    { required: true, message: 'Vui lng nhp mt khu!' },
                    {min:3,message:'Mt khu t nht 3 k t'}
                ]}
                style={{width:'80%'}}
            >
                <Input.Password
                    placeholder="Nhp mt khu"
                    value={password}
                    defaultValue={password}
                    onChange= {(e)=>setpassword(e.target.value)}
                    iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    maxLength={24}
                    style={{height:40}}
                />
            </Form.Item>
            <Form.Item
                name="confirm"
                label="Nhp li mt khu"
                style={{width:'80%'}}
                hasFeedback
                rules={[
                {
                    required: true,
                    message: 'Vui lng nhp li mt khu!',
                },
                ({ getFieldValue }) => ({
                    validator(_, value) {
                    if (!value || getFieldValue('password') === value) {
                        return Promise.resolve();
                    }
                    return Promise.reject(new Error('Mt khu nhp li phi ng nh trn!'));
                    },
                }),
                ]}
            >
                <Input.Password
                    placeholder="Nhp mt khu"
                    value={confirmpass}
                    defaultValue={confirmpass}
                    onChange= {(e)=>setconfirmpass(e.target.value)}
                    iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    maxLength={24}
                    style={{height:40}}
                />
            </Form.Item>
            <Form.Item style={{ padding:"10px 0px" }} >
                <Button htmlType="submit" type="primary" danger style={{ width:100,height:45,borderRadius:8 }}>
                   ng k
                </Button>
            </Form.Item>
            
        </Form>
        </div>
    )
    const ResultSccuess = ()=>(
        <Result
            status="success"
            title="ng k thanh cng !"
            subTitle="Bn c mun ng nhp ngy by gi"
            extra={[
            <Button onClick={handleLogin} type="primary" key="console">
                ng nhp ngay !
            </Button>,
            <Button onClick={props.cancel} key="buy"> sau</Button>,
            ]}
        />
    )
    return(
        <div>
            {success ? 
            <ResultSccuess/>
            :
            <div>
                {SignUp()}
            </div>
            }
           
        </div>
    )

}