import React ,{useEffect,useState,useRef} from 'react';
import * as FetchAPI from '../../util/fetchApi';
import { useSelector } from 'react-redux';
import {Table,Select,message,Modal,Form,Input} from 'antd';
import Spinner from '../../elements/spinner';
import {getColumnSearchProps} from '../../elements/SearchFilter';
import {InfoCircleOutlined} from '@ant-design/icons'
const {Option} = Select;

export default function AccountManger(){
    const [dataUser, setdataUser] = useState();
    const overflowX = useSelector(state=>state.layoutReducer.overflowX);
    const [showContent, setshowContent] = useState(false);
    const [showModalInforAccount, setshowModalInforAccount] = useState(false);
    const [dataInfor, setdataInfor] = useState([]);
    const searchInput = useRef();
    const [formInfor] = Form.useForm();
    useEffect(()=>{
        setshowContent(false);
        getUser();
    },[])
    const getUser = async()=>{
        const res = await FetchAPI.getAPI("/user/getFullUser");
        console.log(res[0])
        setdataUser(res)
        setshowContent(true);
    }
    const updateStatusUser = async(e,id)=>{
        const data = {"id":id,"status":e}
        const res = await FetchAPI.postDataAPI("/user/updateStatusUser",data)
        if(res.msg){
            if(res.msg==="Success"){
                message.success("Cp nht thnh cng");
                getUser();
            }else{
                message.error("C li ri !!")
            }
        }
    }
    const columns = [
        {
            title:"M ti khon",
            key:'id',
            render: record=><span>{record.id}</span>
        },
        {
            title:"Email",
            key:'email',
            ...getColumnSearchProps('email',searchInput)
        },
        {
            title:"Tn khch hng",
            key:'name',
            render: record=><span>{record.name}</span>
        },
        {
            title:"Trng thi",
            key:"status",
            render: record=>(
                <Select 
                    value={record.status}
                    onChange= {(e)=>updateStatusUser(e,record.id)}
                >
                    <Option value={0}>
                        Hot ng
                    </Option>
                    <Option value={1}>
                        Tm kha
                    </Option>
                </Select>
            )
        },
        {
            title:"Chi tit",
            key:'details',
            render: record=>
            <div style={{ paddingLeft:15 }}>
                <InfoCircleOutlined 
                    style={{fontSize:18,cursor:"pointer" }} 
                    onClick={()=>{
                        getInforUser(record.id)
                    }}
                />
            </div>
        }
    ]
    const getInforUser = async(id)=>{
        const data = {"idUser":id}
        const res = await FetchAPI.postDataAPI("/user/getInforUser",data)
        setdataInfor(res[0]);
        formInfor.setFieldsValue(res[0])
        setshowModalInforAccount(true);
    }
    return(
    <div>
        {showContent ?
        <div>
        <Table 
            dataSource={dataUser}
            columns={columns}
            style={overflowX?{overflowX:'scroll'}:null} 
        />
        {showModalInforAccount &&
            <Modal
                title={`Thng tin khch hng ${dataInfor.id}`}
                visible={showModalInforAccount}
                onCancel={()=>setshowModalInforAccount(false)}
                cancelText="Thot"
                okButtonProps={{
                    style: {
                      display: "none",
                    },
                }}
            >
                <Form
                    form={formInfor}
                    labelCol={{ xl:6,md:6,sm:8}}
                    wrapperCol={{ xl:15,md:15,sm:13 }}
                >
                    <Form.Item 
                        label="Email"
                        name="email"
                    >
                        <Input 
                            placeholder="Email khch hng"
                            disabled
                        />
                    </Form.Item>
                    <Form.Item 
                        label="Tn ng nhp"
                        name="username"
                    >
                        <Input 
                            placeholder="Tn ng nhp ca khch hng"
                            disabled
                        />
                    </Form.Item>
                    <Form.Item 
                        label="Tn khch hng"
                        name="name"
                    >
                        <Input 
                            placeholder="Tn khch hng"
                            disabled
                        />
                    </Form.Item>
                    <Form.Item 
                        label="a ch"
                        name="address"
                    >
                        <Input 
                            placeholder="Khch hng cha cung cp thng tin ny"
                            disabled
                        />
                    </Form.Item>
                    <Form.Item 
                        label="S in thoi"
                        name="phone"
                    >
                        <Input 
                            placeholder="Khch hng cha cung cp thng tin ny"
                            disabled
                        />
                    </Form.Item>
                    <Form.Item 
                        label="S n hng"
                        name="totalBill"
                    >
                        <span> {formInfor.getFieldValue(['totalBill'])}</span>
                    </Form.Item>
                    <Form.Item 
                        label="Trng thi"
                        name="status"
                    >
                        {formInfor.getFieldValue(['status'])==0?
                        <span style={{ color:'green',fontWeight:'bold' }}>Hot ng</span>
                        :
                        <span style={{ color:'red',fontWeight:'bold' }}>Tm kha</span>
                        }
                    </Form.Item>
                </Form>
            </Modal>
        }
        </div>
        
        :
        <Spinner spinning={!showContent}/>
        }
    </div>
    )
}