import React,{useEffect,useState} from 'react';
import {Tabs,Table,Button} from 'antd';
import { useSelector } from 'react-redux';
import * as FetchAPI from '../../util/fetchApi';
import {getPriceVND} from '../../contain/getPriceVND';
import {Link} from 'react-router-dom';
import Spinner from '../../elements/spinner';
import moment from 'moment';
const { TabPane } = Tabs;
export default function BillFollow (){
    const datauser = useSelector(state=>state.userReducer.currentUser);
    const [fullData, setfullData] = useState();
    const [showContent, setshowContent] = useState(false);
    const [keyTab, setkeyTab] = useState("1");
    const [statusUser, setstatusUser] = useState(false);
    useEffect(()=>{
        setshowContent(false);
        setstatusUser(false)
        getBill()
    },[datauser])
    const getBill = async()=>{
        if(datauser.id!==undefined){
            try {
                const data = {"idUser":datauser.id}
                const res = await FetchAPI.postDataAPI("/order/getBillByIdUser",data);
                setstatusUser(true)
                setshowContent(true)
                setfullData(res);
            } catch (error) {
                        
            }
        }else{
            setshowContent(true)
        }
    }
    const columns  = [
        {
            title:"n hng",
            key:'id',
            render: record=>{
                return (
                    <span style={{ fontWeight:'bold' }}>{"#"+record.id}</span>
                )
            }
        },
        { 
            title:"Ngy t",
            dataIndex:"",
            key:'date',
            render:(record)=>{
               return(
                   <span>{moment(record.create_at).format('YYYY-MM-DD HH:mm:ss')}</span>
               )
            }   
        },
        { 
            title:"Tnh trng",
            dataIndex:"",
            key:'status',
            render:(record)=>{
                if(record.status===0){
                    return <span style={{ color:'red' }}>ang x l</span>
                }else if(record.status===1){
                    return <sapn style={{color:'blue' }}>ang giao hng</sapn>
                }else if(record.status===2) {
                    return <span style={{ color:'green' }}>Hon thnh</span>
                }else{
                    return <span style={{ color:'gray' }}> hy</span>
                }
            }
        },
        { 
            title:"Tng",
            dataIndex:"",
            key:'total',
            render:(record)=>{
               return <span>{getPriceVND(record.total_price) +" "}</span>
            }
        },
        { 
            title:"Cc thao tc",
            dataIndex:"",
            key:'behavior',
            render:(record)=>{
               return (
               <Button>
                   <Link to={`/billdetails/${record.code_order}`}>
                        Chi tit
                   </Link>
                </Button>
               )
            }
        },
    ]
    const TabContent = ()=>{
        let dataTmp = [];
        if(fullData!==undefined){
            if(keyTab==="1"){
                dataTmp = fullData.filter(e=>e.status!==4);
            }
            if(keyTab==="2"){
                dataTmp = fullData.filter(e=>e.status===0)
            }
            if(keyTab==="3"){
                dataTmp = fullData.filter(e=>e.status===1) 
            }
            if(keyTab==="4"){
                dataTmp = fullData.filter(e=>e.status===2) 
            }
            //sort by date soon as soon
            dataTmp.sort(function(a,b){
                return new Date(b.create_at) - new Date(a.create_at);
            });
        }
        return(
            <div className="wrapperTableBill">
                <Table 
                    columns={columns} 
                    dataSource={dataTmp} 
                    size="small"
                    locale={{ emptyText:"Bn cha c n hng no" }}/>
            </div>
        )
    }
    return(
        <div style={{ minHeight:450 }}>
            {showContent ? 
                <div>
                {statusUser ? 
                <div style={{ backgroundColor:'white',paddingTop:20,minHeight:450 }}>
                <Tabs defaultActiveKey="1" type="card" onChange={(e)=>setkeyTab(e)} size="small" tabPosition="top" centered>
                    <TabPane tab="Tt c n hng ca bn" key="1">
                            <TabContent />
                    </TabPane>
                    <TabPane tab="ang x l" key="2">
                            <TabContent />
                    </TabPane>
                    <TabPane tab="ang giao hng" key="3">
                            <TabContent />
                    </TabPane>
                    <TabPane tab=" hon thnh" key="4">
                            <TabContent />
                    </TabPane> 
                </Tabs>
                </div>
                :
                <div style={{ padding:"20px 40px" }}>
                    <span style={{ fontWeight:'bold' }}>Hy ng nhp  theo di n hng...</span>
                </div>
                }
                </div>
                 :
                <Spinner spinning={!showContent}/>

            }
        </div>
    )
}