import React from 'react';
import {Layout,Row,Col} from 'antd';
import logo from '../images/Fashion-removebg-preview.png';
import {FacebookFilled,GoogleCircleFilled,GithubFilled,TwitterCircleFilled} from '@ant-design/icons'
import {Link} from 'react-router-dom';
const {Footer} = Layout;
export default function FooterWeb (){
    return(
        <Footer className="footer">
            <Row>
                <Col xl={6} className="widget-footer style1">
                    <div>
                        <img className="img-logo" src={logo} width='80%' height='auto' alt="logo"/>
                    </div>
                    
                </Col>
                <Col xl={6} className="widget-footer style2">
                    <h2 className="widget-title-footer">Ti khon</h2>
                    <ul>
                        <li><Link to={{ pathname:"/" }}>ng nhp</Link></li>
                        <li><Link to={{ pathname:"/" }}>ng k</Link></li>
                        <li><Link to={{ pathname:"/billfollow" }}>n hng</Link></li>
                        <li><Link to={{ pathname:"/cart" }}>Gi hng</Link></li>
                    </ul>
                </Col>
                <Col xl={6} className="widget-footer style3">
                <h2 className="widget-title-footer">Ca hng</h2>
                    <ul>
                        <li><Link to={{ pathname:"/" }}>Khm ph</Link></li>
                        <li><Link to={{ pathname:"/" }}>Lin h</Link></li>
                        <li><Link to={{ pathname:"/" }}>Gii thiu</Link></li>
                    </ul>
                </Col>
                <Col xl={6} className="widget-footer style4">
                <h2 className="widget-title-footer">Phn hi vi chng ti</h2>
                <ul>
                    <li><a href="https://www.facebook.com/ken.navi.1/" target="_blank"><FacebookFilled/></a></li>
                    <li><a><GoogleCircleFilled /></a></li>
                    <li><a href="https://github.com/nvchien2811" target="_blank"><GithubFilled /></a></li>
                    <li><a><TwitterCircleFilled /></a></li>
                </ul>
                </Col>
            </Row>
        </Footer>
    )
}