import React ,{useState} from 'react';
import {Modal,Row,Col,Input,Button,Spin,Form,message} from 'antd';
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import * as FetchAPI from '../../util/fetchApi';
import {getUser} from '../../util/getUser'
import { useDispatch } from 'react-redux';
import Register from './Register';

export default function Account(props) {
    const [username, setusername] = useState("");
    const [password, setpassword] = useState("");
    const [spinning, setspinning] = useState(false);
    const [emailRegister, setemailRegister] = useState("");
    const [continueRegister, setcontinueRegister] = useState(false);
    const [formLogin] = Form.useForm();
    const dispatch = useDispatch();
    const handleLoginValidation = ()=>{
        setspinning(true);
        //Validation
        handleLogin();
    }
    const handleLogin = async() =>{
        const data = {"username":username,"password":password};
        const res = await FetchAPI.postDataAPI("/user/login",data);
        console.log(res);
      
        if(res.msg==="Invalid account"){
            message.error("Tn ti khon khng tn ti")
        }else if(res.msg ==="Incorrect password"){
            message.error("Mt khu khng ng")
        }else if(res.msg==="Success"){
            localStorage.setItem("token",res.token);
            finish(res.token);
            message.success("ng nhp thnh cng !");
            formLogin.setFieldsValue({username:"",password:""})
            setusername("");
            setpassword("");
        }
        setspinning(false);
    }
    const finish = async(token)=>{
        getUser(token,dispatch);
        props.refeshAccount();
        props.onCancel();
    }

    const checkEmailExist = async(_,value)=>{
        const data = {"email":value}
        const res = await FetchAPI.postDataAPI("/user/checkEmail",data);
        if(res.success){
            return Promise.resolve();
        }else{
            return Promise.reject(new Error('Email  tn ti trong h thng, vui lng chn email khc  !'));
        }
    }
    const handleRegister = async()=>{
        setcontinueRegister(true)
    }
    const Login = ()=>(
        <Form 
            style={{ paddingBottom:40 }} 
            onFinish={handleLoginValidation} 
            form={formLogin}
            scrollToFirstError
        >
            <p style={{ fontSize:18,fontWeight:'bold' }}>NG NHP</p>
            <p style={{ fontSize:16,fontWeight:'bold' }}>Tn ti khon hoc email ng nhp *</p>
            <Form.Item
                name="username"
                rules={[{ required: true, message: 'Vui lng nhp tn ti khon!' }]}
                style={{width:'80%'}}
            >
                <Input
                    placeholder="Nhp tn ti khon"
                    value={username}
                    defaultValue={username}
                    onChange= {(e)=>setusername(e.target.value)}
                    // maxLength={24}
                    style={{height:40}}
                />
            </Form.Item>
            <p style={{ fontSize:16,fontWeight:'bold' }}>Mt khu *</p>
            <Form.Item
                name="password"
                rules={[
                    { required: true, message: 'Vui lng nhp mt khu!' },
                    {min:3,message:'Mt khu t nht 3 k t'}
                ]}
                style={{width:'80%'}}
            >
                <Input.Password
                    placeholder="Nhp mt khu"
                    value={password}
                    defaultValue={password}
                    onChange= {(e)=>setpassword(e.target.value)}
                    iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
                    // maxLength={24}
                    style={{height:40}}
                />
            </Form.Item>
            <Form.Item style={{ padding:"10px 0px" }} >
                <Button htmlType="submit" type="primary" danger style={{ width:100,height:45,borderRadius:8 }}>
                    ng nhp
                </Button>
            </Form.Item>
            <div>
                <span>Qun mt khu ?</span>
            </div>
        </Form>
    )
    const SignUp = ()=>(
        <Form onFinish={handleRegister}>
            <p style={{ fontSize:18,fontWeight:'bold' }}>NG K</p>
            <p style={{ fontSize:16,fontWeight:'bold' }}>a ch email *</p>
            <Form.Item 
                style={{ width:'80%' }} 
                hasFeedback
                rules={[
                    { type: 'email',message:"Vui lng nhp ng Email"},
                    {required:true,message:"Vui lng in Email !"},
                    { validator: checkEmailExist}
                ]}
                name={['user', 'email']}
            >
                <Input
                    placeholder="Nhp email"
                    value={emailRegister}
                    onChange= {(e)=>setemailRegister(e.target.value)}
                    // maxLength={24}
                    style={{height:40}}
                />
            </Form.Item>
            <p style={{ fontSize:16,padding:"10px 0px" }}>Dng email  ng k vi TheLONA</p>
            <p>Thng tin c nhn ca bn s c s dng  tng tri nghim s dng website, qun l truy cp vo ti khon ca bn, v cho cc mc ch c th khc c m t trong chnh sch ring t.</p>
            <Form.Item style={{ padding:"10px 0px" }}>
                <Button type="primary" danger style={{ width:100,height:45,borderRadius:8 }} htmlType="submit">
                    Tip tc
                </Button>
            </Form.Item>
        </Form>
    )
    return(
        <Modal 
            title="Ti khon" 
            visible={props.visible} 
            onCancel={()=>{props.onCancel();setcontinueRegister(false)}}
            cancelText="Thot"
            footer={false}
            width={1000}
        >
            <Spin spinning={spinning} >
            {!continueRegister ?
            <Row>
                <Col md={12} xs={24}>
                    {Login()}
                </Col>
                <Col md={12} sm={24}>
                    {SignUp()}
                </Col>
            </Row>
            :
            <Register 
                email={emailRegister} 
                back={()=>setcontinueRegister(false)}
                cancel={()=>{props.onCancel();setcontinueRegister(false)}}
                loading={(e)=>setspinning(e)}
                refeshAccount={props.refeshAccount}
            />
            }
           
            </Spin>
        </Modal>
    )
}