import React ,{useEffect,useState}from 'react'; 
import { PageHeader,Table,Row,Col,Space,Card ,Button,message,Modal} from 'antd';
import * as FetchAPI from '../../util/fetchApi';
import {getPriceVND} from '../../contain/getPriceVND';
import Spinner from '../../elements/spinner';
import { useSelector } from 'react-redux';
import { useParams,useHistory } from 'react-router-dom';
import ModalReviewProduct from '../../elements/ModalReviewProduct';
export default function BillDetails(){
    const history = useHistory();
    const [dataProduct, setdataProduct] = useState();
    const [dataBill, setdataBill] = useState();
    const [totalTmp, settotalTmp] = useState(0);
    const [showContent, setshowContent] = useState(false);
    const [promotionprice, setpromotionprice] = useState(0);
    const [showModalReview, setshowModalReview] = useState(false);
    const [showModalCancel, setshowModalCancel] = useState(false);
    const [dataSale, setdataSale] = useState();
    const currentUser = useSelector(state=>state.userReducer.currentUser);
    const [statusUser, setstatusUser] = useState(false);
    const {idBill} = useParams();

    useEffect(()=>{
        console.log(idBill)
        setstatusUser(false);
        setshowContent(false)
        getProduct();
        getInforPayment();
    },[currentUser])
    const getProduct = async()=>{
        const data = {"idOrder":idBill}
        const product = await FetchAPI.postDataAPI('/order/getProductByIdBill',data);
        if(product!==undefined){
            let total = 0;
            product.map((e,index)=>{
                total+= e.price*e.quanity;
                if(index===product.length-1){
                    settotalTmp(total);
                }
                return false;
            })
        }
        setdataProduct(product);
    }
    const getInforPayment = async()=>{
        const data = {"idOrder":idBill}
        const bill = await FetchAPI.postDataAPI('/order/getBillById',data);
        if(currentUser.id===undefined){
            setstatusUser(false)
        }else{
            if(currentUser.id===bill[0].idUser){
                setstatusUser(true)
            }
        }
        setdataBill(bill[0])
        if(bill[0].idSale!==null){
            getSale(bill[0].idSale);
        }
        setshowContent(true)
    }
    const getSale = async(idSale)=>{
        const res = await FetchAPI.postDataAPI("/order/getSaleById",{"idSale":idSale})
        if(res!==undefined){
            setdataSale(res[0])
            setpromotionprice(res[0].cost_sale)
        }
    }
    const handleCancelBill = async()=>{
        const data = {"code_order":dataBill.code_order,"status":3,"email":currentUser.email}
        const res = await FetchAPI.postDataAPI("/order/updateStatusBill",data);
        if(res.msg){
            if(res.msg==="Success"){
                setTimeout(()=>{
                    setshowModalCancel(false)
                    message.success("Hy n hng thnh cng !");
                    history.goBack();
                },500)
            }else{
                setTimeout(()=>{
                    setshowModalCancel(false)
                    message.error("C li ri !!");
                 
                },500)
            }
        }
    }
    const ModalCancelBill = ()=>(
        <Modal
            title={`Bn chc chn mun hy n #${dataBill.id}`}
            visible={showModalCancel}
            onOk={handleCancelBill}
            onCancel={()=>setshowModalCancel(false)}
            cancelText="Thot"
            okText="Chc chn"
        >
            <p>Bn chc chn vi quyt nh ca mnh ! n hng ny ca bn s b hy.</p>
        </Modal>
    )
    const columns  = [
        {
            title:"Sn phm",
            key:'product',
            render : record=>{
                return(
                    <div>
                        <span>{record.name_product+" ( "+record.size +" )"}</span>
                        <span style={{ paddingLeft:20,fontWeight:'bold' }}>{"X" +record.quanity}</span>
                    </div>
                )
            }
        },
        {
            title:"Tng",
            key:'total_price',
            render: record=>{
                return <span>{getPriceVND(record.price*record.quanity) +" "}</span>
            }
        }
    ]
    const ViewProduct = ()=>(
        <Table 
            columns={columns}
            dataSource={dataProduct}
            pagination={false} 
            size="small"
            summary={()=>(
                <Table.Summary>
                    <Table.Summary.Row>
                        <Table.Summary.Cell index={0}><span style={{fontWeight:'bold'}}>Tm tnh</span></Table.Summary.Cell>
                        <Table.Summary.Cell index={1}>{getPriceVND(totalTmp)+" "}</Table.Summary.Cell>
                    </Table.Summary.Row>
                    {dataSale !== undefined &&
                        <Table.Summary.Row>
                            <Table.Summary.Cell index={0}><span style={{fontWeight:'bold'}}>M khuyn mi</span></Table.Summary.Cell>
                            <Table.Summary.Cell index={1}>{"-"+getPriceVND(promotionprice)+" "}</Table.Summary.Cell>
                        </Table.Summary.Row>
                    }
                    <Table.Summary.Row>
                        <Table.Summary.Cell index={0}><span style={{fontWeight:'bold'}}>Ph vn chuyn</span></Table.Summary.Cell>
                        <Table.Summary.Cell index={1}>{getPriceVND(30000)+" "}</Table.Summary.Cell>
                    </Table.Summary.Row>
                    <Table.Summary.Row>
                        <Table.Summary.Cell index={0}><span style={{fontWeight:'bold'}}>Tng</span></Table.Summary.Cell>
                        <Table.Summary.Cell index={1}>{getPriceVND(totalTmp-promotionprice+30000)+" "}</Table.Summary.Cell>
                    </Table.Summary.Row>
                    {dataBill.status===2 &&
                    <Table.Summary.Row>
                        <Table.Summary.Cell index={0}><span style={{fontWeight:'bold'}}>nh gi sn phm</span></Table.Summary.Cell>
                        <Table.Summary.Cell index={1}>
                            <Button onClick={()=>setshowModalReview(true)}>
                                nh gi ngay
                            </Button>
                        </Table.Summary.Cell>
                    </Table.Summary.Row>
                    }
                </Table.Summary>
        )}
        />      
    )
    const getTextStatus = (a)=>{
        if(a===0){
            return <b>ang x l</b>
        }else if(a===1){
            return <b>ang giao hng</b>
        }else if(a===2){
            return <b> hon thnh</b>
        }else{
            return <b> hy</b>
        }
    }

    console.log(dataBill);
    return(
        <div style={{ minHeight:450,paddingBottom:20 }}>
            {showContent ?
            <div>
            {statusUser ?
            <div>
                
            <PageHeader
                className="site-page-header"
                onBack={() => history.goBack()}
                title="Chi tit n hng"
                subTitle={"M n hng: #"+dataBill.id}
            />
            <Row>
            <Col lg={14} xs={24} style={{ padding:"20px 40px" }} >
                {ViewProduct()}
                <Card title="a ch thanh ton" style={{ marginTop:30 }}>
                <div style={{ fontSize:16 }}>
                <Space direction="vertical" size={20}>
                    <span><b>Tn: </b>{dataBill.name}</span>
                    <span><b>a ch: </b>{dataBill.address}</span>
                    <span><b>Email: </b>{dataBill.email}</span>
                    <span><b>S in thoi: </b>{dataBill.phone} </span>
                    <span><b>Message: </b>{dataBill.message} </span>
                </Space>
                </div>
                </Card>
            </Col>
            <Col lg={10} xs={24} style={{ justifyContent:'center',display:'flex' }}>
                <Card title="Cm n bn. n hng  c nhn." style={{ marginTop:20,width:'80%' }}>
                <ul>
                    <Space size={10} direction="vertical">
                        <li>M n hng : <b>{"#"+dataBill.id}</b></li>
                        <li>Ngy t: <b>{new Date(dataBill.create_at).toString()}</b></li>
                        <li>Email : <b>{dataBill.email}</b></li>
                        <li>Tng cng : <b>{getPriceVND(totalTmp-promotionprice)+" "}</b></li>
                        <li>Thi gian cp nht ha n: <b>{new Date(dataBill.update_at).toString()}</b></li>
                                                <li>Phng thc thanh ton: 
                                                <b>{dataBill.method_payment === 1 && "Thanh ton khi nhn hng"}</b>
                                <b>{dataBill.method_payment === 2 && "Chuyn khon ngn hng"}</b>
                                <b>{dataBill.method_payment === 3 && "Thanh ton VNPAY"}</b>
                                <b>{dataBill.method_payment === 4 && "Thanh ton MOMO"}</b>
                                <b>{dataBill.method_payment===6 && "Thanh ton PayPal"}</b>
                            
                        </li>
                        <li>
                            Tnh trng : {getTextStatus(dataBill.status)}
                        </li>
                        <div>
                            <Button type="primary" onClick={()=>setshowModalCancel(true)} danger disabled={dataBill.status!==0} >
                                Hy n
                            </Button>
                        </div>
                    </Space>
                </ul>
                </Card>
            </Col>
            </Row> 
            <ModalReviewProduct 
                visible={showModalReview}
                onCancel={()=>setshowModalReview(false)}
                refresh={()=>getProduct()}
                dataProduct={dataProduct}
                user={currentUser}
            /> 
            {ModalCancelBill()}
            </div>
            :
            <div style={{ padding:"20px 40px" }}>
                <span style={{ fontWeight:'bold' }}>Bn khng c quyn truy cp ha n ny...</span>
            </div>
            }
            </div>
            :
            <Spinner spinning={!showContent}/>
            }
        </div>
    )
}